.container {
  border: solid 1px #aaa;
  border-radius: 20px;
  padding: 8px;
  margin: 8px;
  width: 300px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.title {
  margin: 0;
  color: #aaa;
}

.button {
  background-color: #ddd;
  border: none;
  padding: 8px;
  border-radius: 8px;
  &:hover {
    background-color: rgb(70, 12, 12);
    color: red;
    cursor: grabbing;
  }
}
